
.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#121212, #474038);
}

.title {
    font-size: 70px;
    text-transform: uppercase;
    color: #FFF;
    font-style: italic;
}

.icon-logo {
    margin-left: 9px;
}

.logo {
    font-size: 65px;
    color: #0672ff;
}

.containerInput {
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 34px 0;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgba(0,0,0, 0.5);
}

.containerInput input {
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: #FFF;
    outline: none;
    margin-right: 8px;
}

.containerInput input::placeholder {
    color: #f1f1f1;
}

.buttonSearch {
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.5s;
}

.buttonSearch:hover {
    transform: scale(1.2);
}


.main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #FFF;
    width: 500px;
    border-radius: 8px;
    box-shadow: 12px 12px 12px rgba(0,0,0, 0.5);
}

.main h2 {
    margin: 16px 0;
    font-size: 34px;

}

.main span {
    margin-bottom: 16px;
    font-weight: bold;
}

strong {
    color: #0672ff;
}

@media (max-width: 620px) {
    .title {
        font-size: 40px;
    }

    .logo {
        font-size: 40px;
        margin: none;
    }

    .main h2 {
        font-size: 28px;
    }

    .main {
        width: 80%;
        min-height: 250px;
    }
}

@media (max-width: 375px) {
    .title {
        font-size: 30px;
    }

    .logo {
        font-size: 30px;
        margin: none;
    }
}